const WidgetsMap: { [key: string]: any } = {};

function widget(key): ClassDecorator {
    return function(target: object) {
        WidgetsMap[key] = target;
    };
}

function createWidget(config: any) {
    const { type = 'bi.label' } = config;
    if (WidgetsMap[type]) {
        return new WidgetsMap[type](config);
    } else {
        return null;
    }
}

export { widget, createWidget };
